.navigation {
  padding: 50px 0;
  &-list {
    li a {
      font-size: 20px;
      line-height: 120%;
      color: #E1E1E1;
    }
    li:not(:first-child) {
      margin-top: 15px;
    }
    li a:hover {
      opacity: 0.5;
    }
  }
}
.content {
  overflow: hidden;
}
.item {
  .hide {
    z-index: 5;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  &--active {
    .hide {
      opacity: 1;
      pointer-events: all;
    }
  }
}
.btn-blue {
  background: #0055F6;
  span {
    font-size: 18px;
    line-height: 120%;
    font-weight: 600;
    color: #FFF;
  }
  &:hover {
    background: #0039b3;
  }
}
.pages {
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 120%;
  color: #A7A7A7;
  span {
    padding: 0 3px;
  }
}
.pagination {
  &__items {
    display: flex;
    align-items: center;
  }
  &__item {
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    line-height: 120%;
    color: #979797;
    &:hover {
      color: #ffffff;
    }
    &--active {
      color: #ffffff;
      border: 1px solid rgba(128, 128, 128, 0.30);
      background: rgba(255, 255, 255, 0.05);
    }
  }
  &__prev,
  &__next {
    svg path {
      transition: all 0.5s;
    }
    &:hover {
      svg path {
        fill-opacity: 1;
      }
    }
  }
}
.blue {
  background: #3263C0;
}
.green {
  background: #43B262;
}
.violet {
  background: #AF69C8;
}
.yellow {
  background: #FAC743;
}
.orange {
  background: #FC973F;
}
.red {
  background: #FF524C;
}