.dashboard {
  padding: 60px 0;
  &__title {
    font-size: 40px;
    line-height: 120%;
    font-weight: 600;
    letter-spacing: -0.8px;
    color: #FFF;
  }
  &__top {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 40px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 15px;
      width: 100%;
      height: 1px;
      background: #222;
    }
  }
  &__left {
    display: flex;
  }
  &__right {
    margin-bottom: 30px;
  }
  &-search {
    flex-shrink: 0;
    width: 17px;
    cursor: pointer;
    svg {
      width: 100%;
      height: auto;
      g {
        transition: all 0.5s;
      }
    }
    &:hover {
      svg g {
        opacity: 1;
      }
    }
  }
  &__links {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-left: 35px;
    .blue {
      &:after {
        background: #3263C0;
      }
    }
    .green {
      &:after {
        background: #43B262;
      }
    }
  }
  &__link {
    display: flex;
    flex-shrink: 0;
    align-items: center;
    font-family: 'Sora';
    font-size: 14px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    background: transparent;
    color: #CDCDCD;
    margin: 0 30px 30px 0;
    &:hover {
      color: #ffffff;
    }
    &:after {
      content: '';
      display: inline-block;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      margin-left: 8px;
    }
  }
  &__link--active {
    color: #FFF;
    position: relative;
    &:after {
      display: none;
    }
    &:before {
      content: '';
      position: absolute;
      left: 50%;
      bottom: -15px;
      transform: translate(-50%, 0);
      width: 100%;
      height: 2px;
      background: #FFF;
    }
  }
  &__add {
    position: relative;
    &-show {
      display: flex;
      align-items: center;
      font-size: 12px;
      line-height: 120%;
      font-weight: 600;
      letter-spacing: 0.6px;
      text-transform: uppercase;
      color: rgba(255, 255, 255, 0.50);
      cursor: pointer;
      transition: all 0.5s;
      &:hover {
        color: #ffffff;
        span {
          border: 1px solid rgba(255, 255, 255, 1);
        }
      }
      span {
        width: 15px;
        height: 15px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid rgba(255, 255, 255, 0.50);
        border-radius: 50%;
        margin-right: 4px;
        transition: all 0.5s;
        margin-top: -2px;
      }
    }
    &-hide {
      position: absolute;
      top: 100%;
      right: 0;
      width: 225px;
      padding-top: 10px;
    }
    &-wrap {
      background: #2E2E2E;
      padding: 15px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: -18px;
        right: 12px;
        border: 10px solid transparent;
        border-bottom: 10px solid #2E2E2E;
        pointer-events: none;
      }
    }
    .change-labels__top {
      padding-bottom: 15px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.08);
    }
    .change-labels__bottom {
      padding-top: 15px;
    }
    .change-labels__title {
      font-size: 12px;
      line-height: 120%;
      font-weight: 600;
      letter-spacing: 0.6px;
      text-transform: uppercase;
      color: rgba(255, 255, 255, 0.50);
    }
    .change-labels__name {
      display: flex;
      align-items: center;
      background: #DADADA;
      padding: 5px 8px;
      margin-top: 15px;
      span {
        font-size: 14px;
        line-height: 120%;
        color: #151515;
      }
    }
    .change-labels__checkboxes {
      display: flex;
      align-items: center;
      margin-top: 10px;
    }
    .change-labels__checkbox {
      position: relative;
      width: 14px;
      height: 14px;
      &:not(:first-child) {
        margin-left: 10px;
      }
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        opacity: 0;
        cursor: pointer;
        &:checked ~ label {
          &:after {
            opacity: 1;
            pointer-events: all;
          }
        }
      }
      label {
        display: block;
        width: 14px;
        height: 14px;
        border-radius: 50%;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: 8px;
          height: 5px;
          background-image: url("../img/check.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
          opacity: 0;
          pointer-events: none;
          transition: all 0.3s;
        }
      }
    }
    .change-labels__btn {
      width: 106px;
      height: 30px;
      margin-top: 10px;
    }
    .change-labels__items {
      display: grid;
      grid-template-columns: 100%;
      gap: 8px;
    }
    .change-labels__item {
      display: flex;
      align-items: center;
      &-btn {
        width: 10px;
        cursor: pointer;
        &:hover {
          svg path {
            stroke: #ffffff;
          }
        }
        svg {
          width: 100%;
          height: auto;
          path {
            transition: all 0.5s;
          }
        }
      }
      &-text {
        display: flex;
        align-items: center;
        font-size: 14px;
        line-height: 120%;
        color: #CDCDCD;
        margin-left: 8px;
        span {
          width: 14px;
          height: 14px;
          border-radius: 50%;
          margin-top: -2px;
          margin-right: 5px;
        }
      }
    }
    .delete-item--active {
      display: none;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    margin-top: 25px;
  }
  &-card {
    background: #111;
    padding: 15px 60px 0 20px;
    cursor: pointer;
    transition: all 0.5s;
    position: relative;
    &:hover {
      background: #1F1F1F;
      .dashboard-card__arrow2 {
        background: #1F5EFF;
        svg path {
         fill-opacity: 1;
        }
      }
    }
    &__top {
      display: flex;
      justify-content: space-between;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: -20px;
        bottom: 0;
        width: calc(100% + 40px);
        height: 1px;
        background: #323232;
      }
      .dashboard-card__group {
        margin-bottom: 15px;
      }
    }
    &__bottom {
      display: flex;
      justify-content: space-between;
      padding-top: 15px;
    }
    &__left {
      flex-shrink: 0;
      width: 300px;
    }
    &__center {
      width: calc(100% - 400px);
      display: flex;
      flex-wrap: wrap;
      margin: 0 100px;
    }
    &__right {
      width: 100px;
    }
    &__title {
      display: flex;
      align-items: center;
      &-text {
        font-family: 'Sora';
        font-size: 18px;
        line-height: 120%;
        font-weight: 600;
        color: #ffffff;
      }
      &-text2 {
        font-size: 10px;
        line-height: 120%;
        font-weight: 700;
        text-transform: uppercase;
        color: #484848;
        margin-right: 6px;
      }
      &-icons {
        display: flex;
        align-items: center;
        margin-left: 15px;
      }
      .dashboard-card__shapes {
        display: flex;
        align-items: center;
        .blue,
        .green,
        .violet,
        .yellow,
        .orange,
        .red {
          width: 9px;
          height: 9px;
          border-radius: 50%;
          margin-right: 6px;
        }
      }
    }
    &__group {
      display: flex;
      flex-wrap: wrap;
      &:not(:last-child) {
        margin-right: 60px;
      }
    }
    &__info {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      &:not(:first-child) {
        margin-left: 15px;
      }
    }
    &__info-title {
      font-family: 'Sora';
      font-size: 10px;
      line-height: 120%;
      font-weight: 600;
      text-transform: uppercase;
      color: #7C7C7C;
    }
    &__info-text {
      font-family: 'Sora';
      font-size: 10px;
      line-height: 120%;
      font-weight: 600;
      color: #CDCDCD;
      margin-left: 5px;
    }
    &__docs {
      display: flex;
      align-items: center;
    }
    &__doc {
      display: flex;
      align-items: center;
      &:not(:first-child) {
        margin-left: 12px;
      }
      i {
        flex-shrink: 0;
        width: 13px;
        margin-right: 3px;
        svg path {
          transition: all 0.5s;
        }
      }
      span {
        font-size: 10px;
        line-height: 120%;
        font-weight: 600;
        text-transform: uppercase;
        color: #848484;
        transition: all 0.5s;
      }
      &:hover {
        span {
          color: #CDCDCD;
        }
        svg .path1 {
          fill: #B8B8B8;
        }
      }
    }
    &__subtitle {
      font-family: 'Sora';
      font-size: 10px;
      line-height: 120%;
      font-weight: 600;
      text-transform: uppercase;
      color: #7C7C7C;
    }
    &__status {
      display: flex;
      align-items: center;
      margin-top: 5px;
    }
    &__passive {
      font-size: 16px;
      line-height: 120%;
      font-weight: 700;
      color: #0055F6;
    }
    &__active {
      display: flex;
      align-items: center;
      margin-left: 30px;
      position: relative;
      &-show {
        display: flex;
        align-items: center;
        font-size: 16px;
        line-height: 120%;
        font-weight: 700;
        color: #444;
        transition: all 0.3s;
        span {
          width: 11px;
          height: 11px;
          display: flex;
          justify-content: center;
          align-items: center;
          border: 1px solid #444;
          border-radius: 50%;
          font-size: 8px;
          margin-left: 4px;
        }
        &:hover {
          color: #ffffff;
          span {
            border-color: #ffffff;
          }
        }
      }
      .show--active {
        color: #0055F6;
        span {
          color: #444;
        }
        &:hover {
          span {
            color: #ffffff;
            border-color: #ffffff;
          }
        }
      }
      &-hide {
        position: absolute;
        left: -15px;
        bottom: 100%;
        width: 273px;
        padding-bottom: 15px;
      }
      &-wrap {
        background: #2E2E2E;
        padding: 15px;
        font-size: 14px;
        line-height: 140%;
        color: #AAA;
        position: relative;
        a {
          font-size: 16px;
          text-decoration-line: underline;
          color: #F2F2F2;
        }
        &:after {
          content: '';
          position: absolute;
          left: 12px;
          bottom: -18px;
          border: 10px solid transparent;
          border-top: 10px solid #2E2E2E;
          pointer-events: none;
        }
      }
    }
    .item {
      &--active .dashboard-card__active-show {
        color: #ffffff;
        span {
          border-color: #ffffff;
        }
      }
    }
    &__info2 {
      display: flex;
      flex-direction: column;
      margin: 0 40px 15px 0;
      &-title {
        font-family: 'Sora';
        font-size: 10px;
        line-height: 120%;
        font-weight: 600;
        text-transform: uppercase;
        color: #7C7C7C;
        margin-bottom: auto;
      }
      &-text {
        display: flex;
        align-items: center;
        margin-top: 3px;
        span {
          font-family: 'Sora';
          font-size: 20px;
          line-height: 120%;
          font-weight: 600;
          color: #CDCDCD;
          margin-right: 3px;
        }
      }
    }
    &__arrow2 {
      position: absolute;
      top: 0;
      right: 0;
      width: 40px;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #1A1A1A;
      transition: all 0.5s;
    }
    &__arrow {
      position: relative;
      .blue {
        &:after {
          background: #3263C0;
        }
      }
      .green {
        &:after {
          background: #43B262;
        }
      }
    }
    &__arrow-hide {
      position: absolute;
      left: -15px;
      bottom: 100%;
      width: 200px;
      padding-bottom: 15px;
    }
    &__arrow-wrap {
      background: #2E2E2E;
      padding: 15px;
      font-size: 14px;
      line-height: 140%;
      color: #AAA;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 12px;
        bottom: -18px;
        border: 10px solid transparent;
        border-top: 10px solid #2E2E2E;
        pointer-events: none;
      }
    }
    .change-check__title {
      font-size: 12px;
      line-height: 120%;
      font-weight: 600;
      letter-spacing: 0.6px;
      text-transform: uppercase;
      color: rgba(255, 255, 255, 0.50);
    }
    .change-check__items {
      display: grid;
      grid-template-columns: 100%;
      gap: 8px;
      margin-top: 8px;
    }
    .change-check__item {
      display: flex;
      align-items: center;
    }
    .change-check__text {
      font-size: 14px;
      line-height: 120%;
      background: transparent;
      color: #CDCDCD;
      position: relative;
      padding-left: 20px;
      margin-left: 8px;
      &:after {
        content: '';
        position: absolute;
        top: 1px;
        left: 0;
        width: 14px;
        height: 14px;
        border-radius: 50%;
      }
    }
    .change-check {
      width: 25px;
      height: 12px;
      position: relative;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        padding: 0;
        margin: 0;
        cursor: pointer;
        &:checked ~ label {
          background: #0055F6;
          &:after {
            left: calc(100% - 11px);
            background: #ffffff;
          }
        }
      }
      label {
        display: block;
        width: 100%;
        height: 100%;
        background: #0D0E12;
        border-radius: 12px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 3px;
          transform: translate(0, -50%);
          width: 8px;
          height: 8px;
          background: #606060;
          border-radius: 50%;
          transition: all 0.5s;
        }
      }
    }
  }
  &__bottom {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
  }
  .pages {
    margin: 0 50px 15px 0;
  }
  &__sorting {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    &-text {
      font-size: 14px;
      line-height: 120%;
      color: #8C8C8C;
    }
    &-checkboxes {
      display: flex;
      align-items: center;
      margin-left: 20px;
    }
    &-checkbox {
      position: relative;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        opacity: 0;
        cursor: pointer;
        &:checked ~ label {
          border: 1px solid rgba(128, 128, 128, 0.60);
          background: rgba(255, 255, 255, 0.06);
          color: #ffffff;
        }
      }
      label {
        width: 32px;
        height: 32px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 14px;
        line-height: 120%;
        color: #979797;
      }
    }
  }
  .pagination {
    margin: 0 50px 15px 0;
  }
}

@media screen and (max-width: 1400px) {
  .dashboard {
    &-card {
      &__center {
        margin: 0 30px;
      }
    }
  }
}

@media screen and (max-width: 960px) {
  .dashboard {
    &__top {
      flex-direction: column;
      align-items: start;
    }
    &__add-hide {
      left: calc(100% - 40px);
      right: unset;
    }
    &__add-wrap {
      &:after {
        right: unset;
        left: 12px;
      }
    }
    &__cards {
      overflow-x: auto;
      padding-bottom: 10px;
      &::-webkit-scrollbar {
        height: 2px;
      }
      &::-webkit-scrollbar-track {
        background: #7C7C7C;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #ffffff;
        border-radius: 3px;
      }
    }
    &-card {
      width: 930px;
      overflow: hidden;
      &__arrow-hide {
        top: 0;
        left: calc(100% + 15px);
        bottom: unset;
        width: 215px;
        padding: 0 0 0 15px;
      }
      &__active-hide {
        top: -70px;
        left: calc(100% + 15px);
        bottom: unset;
        width: 290px;
        padding: 0 0 0 15px;
      }
    }
  }
}